  display: grid;
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .cards {
    grid-template-columns: repeat(3, 1fr);
  }
}

