

.nav__list {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}

.nav__item {
  margin-left: 20px;
  transition: transform 300ms;
}

.nav__item:first-child {
  margin-left: 0;
}

.is-active {
  color: $blue-accent;
}

.is-active::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 2px;
  background-color: $blue-accent;
}

.nav__link {
  position: relative;
  display: flex;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
  text-decoration: none;
  text-transform: uppercase;
  color: $main-accent;
  transition: color 0.3s;
}

.nav__link:hover {
  color: $blue-accent;
}

.nav-hover:hover {
  color: $blue-accent;
}
