@import 'variables';

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  background: $white;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0 , 0.05);
  flex-shrink: 0;
}

.logo {
  display: block;
  height: 40px;
  width: 40px;
}

.logo__image {
  height: 100%;
  width: 100%;
}
